.header-text {
    font-family: 'Roboto';
    font-size: 2rem;
    font-weight: 700;
    letter-spacing: .2rem;
    text-decoration: none;
    justify-content: center;
    color: inherit;
    display: inline-block;
    text-align: center;
    padding: 1vh 1vw;
}

.title-hero {
padding: 8px;
border-radius: 4px;
background-color: #bfe1d9;
color:black;
font-family: 'Roboto';
box-shadow: 0 0 5px rgba(0,0,0,0.5);
}

.story-intro {
padding-left: 20%;
padding-right: 20%;
padding-bottom: 100px;
}

.intro-card {
text-align: center;
margin-top: 60px;
margin-bottom: 0px !important;
padding-top: 4%;
padding-left: 4%;
padding-right: 4%;
}

.data-text {
text-align: center;
border: 2px solid rgba(0, 0, 0, 0.4);
height: 100%;
min-height: 120px;
border-radius: 5px;
}

.home-header {
padding-top: 50px;
padding-left: 12%;
padding-right: 12%;
padding-bottom: 50px;
display: flex;
justify-content: space-between;
}

.data-at-a-glance {
font-family: 'Roboto';
text-align: center;
}

.title {
text-align: center;
/* margin-top: 4%; */
border-radius: 5px;
/* max-width: 500px; */
/* margin-right: 30px; */
}

.slf-padding {
padding-top: 30px;
padding-bottom: 30px;
}

.story-budget-break {
padding-left: 15%;
padding-right: 15%;
padding-bottom: 100px;
}

.vertical-divider {
border-left: 2px solid rgba(169, 176, 183);
height: 100px;
position: absolute;
left: 50%;
}

.red-bg {
background-color: rgb(172, 43, 55);
}

.video {
padding: 7%;
}

.padding-grey {
padding: 3%;
}

.center {
display: flex;
text-align: center;
align-content: center;
}

.border {
border: 2px solid rgba(169, 176, 183, 0.3);
}

/* SPACY ONLY STYLING*/
.ner-submit {
margin-top: 20px;
}

.spacy-output {
margin: 40px;
}

.data-stats {
display: grid;
direction: row;
justify-content: space-around;
align-items: center;
padding-top: 20px;
}
.center {
    display: flex;
    text-align: center;
    align-content: center;
    justify-content: center;
    padding: 20px;
    }
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

